<template>
	<view class="mode-title" :style="titleStyle">
		<view class="mode-title-left" :style="borderStyle"></view>
		<view class="mode-title-text">
			<text v-if="title">{{ title }}</text>
			<slot></slot>
		</view>
	</view>
</template>

<script>
	export default {
		computed: {
			titleStyle() {
				return {
					fontSize: this.titleFontSize + "rpx",
					color: this.titleColor,
					backgroundColor: this.bgColor,
				};
			},

			borderStyle() {
				return {
					width: this.borderWidth + "rpx",
					backgroundColor: this.borderColor,
				};
			},
		},

		props: {
			// 标题
			title: {
				type: String,
				default: "",
			},

			// 左边边框宽度
			borderWidth: {
				type: [String, Number],
				default: 8,
			},

			// 左边边框颜色
			borderColor: {
				type: String,
				default: "#f90",
			},

			// 背景颜色
			bgColor: {
				type: String,
				default: "#fff",
			},

			// 字体颜色
			titleColor: {
				type: String,
				default: "#f90",
			},

			// 标题字体大小
			titleFontSize: {
				type: [Number, String],
				default: 32,
			},
		},
	};
</script>

<style lang="less" scoped>
	.mode-title {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		padding: 20rpx 0;

		.mode-title-left {
			height: 24rpx;
			margin-right: 20rpx;
			margin-left: 20rpx;
		}

		.mode-title-text {
			.flexgrow();
			font-size: @uni-font-size-lg;
		}
	}
</style>
